'use client'

import Image from 'next/image'
import { useTranslation } from 'next-i18next'
import { places } from '../page'
import { Swiper, SwiperSlide } from 'swiper/react'
import { Navigation } from 'swiper/modules'
import Link from 'next/link'

import 'swiper/css'
import 'swiper/css/navigation'

export default function PlacePage({ params }: { params: { id: string } }) {
  const placeId = decodeURIComponent(params.id).replace(/-/g, ' ')
  const place = places.find(p => p.name.toLowerCase() === placeId.toLowerCase())

  if (!place) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <p className="text-red-500 text-xl">{t('placeNotFound')}</p>
      </div>
    )
  }

  const { t } = useTranslation()

  // Filter other places for the recommendations slider
  const otherPlaces = places.filter(p => p.name !== place.name)

  return (
    <div className="min-h-screen bg-white">
      <div className="max-w-7xl mx-auto p-6">
        {/* Main Place Info */}
        <div className="grid md:grid-cols-2 gap-10 mb-16">
          <div className="rounded-xl overflow-hidden shadow-lg border border-gray-200">
            <Image
              src={place.image}
              alt={place.name}
              width={800}
              height={600}
              className="w-full h-auto object-cover"
              priority
            />
          </div>
          
          <div className="flex flex-col justify-center space-y-6">
            <h1 className="text-3xl font-bold text-gray-800">{place.name}</h1>
            <p className="text-gray-600 text-lg">{place.description}</p>
            
            <div className="space-y-4">
              <p className="text-lg font-medium">{place.travelTime}</p>
              <p className="text-lg font-medium">{place.price}</p>
            </div>

            <button className="bg-indigo-600 hover:bg-indigo-700 text-white px-6 py-3 rounded-md text-lg font-medium w-fit">
              {t('bookNow')}
            </button>
          </div>
        </div>

        {/* Recommendations Slider */}
        <h2 className="text-2xl font-bold mb-6">{t('youMayAlsoLike')}</h2>
        <div className="pb-12">
          <Swiper
            modules={[Navigation]}
            spaceBetween={30}
            slidesPerView={1}
            navigation
            breakpoints={{
              640: { slidesPerView: 2 },
              1024: { slidesPerView: 3 }
            }}
          >
            {otherPlaces.map((recPlace) => (
              <SwiperSlide key={recPlace.name}>
                <Link href={`/${recPlace.name.toLowerCase().replace(/\s+/g, '-')}`}>
                  <div className="rounded-lg overflow-hidden shadow-md border border-gray-200 h-full flex flex-col">
                    <div className="relative h-48">
                      <Image
                        src={recPlace.image}
                        alt={recPlace.name}
                        fill
                        className="object-cover"
                      />
                    </div>
                    <div className="p-4">
                      <h3 className="font-bold">{recPlace.name}</h3>
                      <p className="text-sm text-gray-600 mt-1">{recPlace.description}</p>
                    </div>
                  </div>
                </Link>
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
      </div>
    </div>
  )
}